.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1;
  display: none;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 50%;
  height: 100vh;
  border: none;
  border-radius: 6px;
  background: #232323;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  padding: 0;
  overflow: hidden;
  z-index: 1;
}
@media screen and (max-width: 768px) {
  .backdrop {
    display: block;
  }
  .modal {
    display: flex;
  }
}
